import React from 'react'
// Components
import { SingleProject } from '../components/SingleProject'
// Data 
import { projects } from '../data'


export const Home = ({ setOverlayOpen, setGallery }) => {
  return (
    <>
      <div className="intro">
        <p>Below are the projects that I have done. You can click on the below images to see more screenshots from the projects or you can visit the live pages or the source codes if you will. The projects using external APIs only have source codes and not live pages to avoid the unnecessary exploitation of their server capacity. However the source codes are available and full functional.</p>
      </div>
      <div className="projects-container">
        {projects.map((project) => {
          return <SingleProject key={project.id} project={project} setOverlayOpen={setOverlayOpen} setGallery={setGallery} />
        })}
      </div>
    </>
  )
}
