import React from 'react'

export const SingleProject = ({ project, setOverlayOpen, setGallery }) => {
  const appearOverlay = () => {
    let arr = []
    if (Object.values(project.otherImages)[0]) { arr.push(Object.values(project.otherImages)[0]) }
    if (Object.values(project.otherImages)[1]) { arr.push(Object.values(project.otherImages)[1]) }
    if (Object.values(project.otherImages)[2]) { arr.push(Object.values(project.otherImages)[2]) }
    if (Object.values(project.otherImages)[3]) { arr.push(Object.values(project.otherImages)[3]) }
    if (Object.values(project.otherImages)[4]) { arr.push(Object.values(project.otherImages)[4]) }
    if (Object.values(project.otherImages)[5]) { arr.push(Object.values(project.otherImages)[5]) }
    if (Object.values(project.otherImages)[6]) { arr.push(Object.values(project.otherImages)[6]) }
    setGallery(arr)
    setOverlayOpen(true)
  }

  return (
    <div className="single-project">
      <div className="image-container">
        <img className="main-image" src={project.mainImage} alt="" />
        <div className="other-images" onClick={() => { appearOverlay() }}>
          {Object.values(project.otherImages)[0] && <img className="other-image" src={Object.values(project.otherImages)[0]} alt="" />}
          {Object.values(project.otherImages)[1] && <img className="other-image" src={Object.values(project.otherImages)[1]} alt="" />}
          {Object.values(project.otherImages)[2] && <img className="other-image" src={Object.values(project.otherImages)[2]} alt="" />}
          {Object.values(project.otherImages)[3] && <img className="other-image" src={Object.values(project.otherImages)[3]} alt="" />}
          {Object.values(project.otherImages)[4] && <img className="other-image" src={Object.values(project.otherImages)[4]} alt="" />}
          {Object.values(project.otherImages)[5] && <img className="other-image" src={Object.values(project.otherImages)[5]} alt="" />}
          {Object.values(project.otherImages)[6] && <img className="other-image" src={Object.values(project.otherImages)[6]} alt="" />}
          {Object.values(project.otherImages)[7] && <img className="other-image" src={Object.values(project.otherImages)[7]} alt="" />}
        </div>
      </div>
      <div className="project-buttons">

        <button className={project.visitLiveLink === '' ? 'inactive-button visit-button' : 'visit-button'}>
          Visit Page  <a href={project.visitLiveLink} target="_blank" rel="noreferrer"> </a>
        </button>

        <button className="visit-button">
          Visit Code <a href={project.visitCodeLink} target="_blank" rel="noreferrer"> </a>
        </button>

      </div>
      <div className="info-container">
        <h2 className="title">{project.projectName}</h2>

        <div className="icon-container">
          {Object.values(project.icons)[0] && <img className="icon" src={Object.values(project.icons)[0]} alt="" />}
          {Object.values(project.icons)[1] && <img className="icon" src={Object.values(project.icons)[1]} alt="" />}
          {Object.values(project.icons)[2] && <img className="icon" src={Object.values(project.icons)[2]} alt="" />}
        </div>

        <p className="brief">{project.description}</p>
      </div>
      <div className="keyword-container">
        {project.keywords.map((keyword) => {
          let id = project.keywords.indexOf(keyword)
          return <div className="single-keyword" key={id}>{keyword}</div>
        })}
      </div>
    </div>
  )
}

