import React, { useState } from 'react'
// Router
import { HashRouter, Route, Switch, Link } from "react-router-dom";
// Pages
import { Home } from './pages/Home'
import { CV } from './pages/CV'
// Components
import { Gallery } from './components/Gallery'
// Styling
import './App.css'

function App() {

  const [overlayOpen, setOverlayOpen] = useState(false)
  const [gallery, setGallery] = useState([])

  return (
    <>
      <HashRouter>
        <h2 className="page-title">Aykut </h2>

        {overlayOpen ? <div className="overlay"><Gallery gallery={gallery} setOverlayOpen={setOverlayOpen} /></div> : ''}

        <div className="header">
          <div className="header-button-container">
            <div className="header-button"><Link to="/" className="header-link"></Link>Projects</div>
            <div className="header-button">CV <Link to="/CV" className="header-link"></Link></div>
          </div>
        </div>
        <Switch>

          <Route exact path="/">
            <Home setOverlayOpen={setOverlayOpen} setGallery={setGallery} />
          </Route>

          <Route path="/CV">
            <CV />
          </Route>

        </Switch>
      </HashRouter>
    </>
  );
}

export default App;
