import React, { useState } from 'react'
// Icons 
import HighlightOffIcon from '@material-ui/icons/HighlightOff';

export const Gallery = ({ gallery, setOverlayOpen }) => {

  const [index, setIndex] = useState(0)

  return (
    <>
      <div className="overlay-image-container">
        <img src={gallery[index]} alt="" className="overlay-image" />
        <div className="bottom-gallery">
          {gallery.map((source) => {
            let id = gallery.indexOf(source)
            return <img src={source} alt="" className="bottom-gallery-image" key={id} onClick={() => { setIndex(id) }} />
          })}
        </div>
        <div className="close-button-container" onClick={() => setOverlayOpen(false)}>
          Close Gallery
          <HighlightOffIcon className="close-button" /></div>
      </div>
    </>
  )
}
